/*head*/
.fixed {
    position: fixed;
    left: 0;
    top: 0;
    -webkit-box-shadow: 0 0 10px rgba(0,0,0,0.3);
    -moz-box-shadow: 0 0 10px rgba(0,0,0,0.3);
    box-shadow: 0 0 10px rgba(0,0,0,0.3);
    z-index: 2;
}
.page {
    width: 100%;
    height: 100%;
    color: #333;
    background: white;
    z-index: 2;
}
.mask {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background: rgba(0,0,0,0);
}

.content {
    font-size: 0.3rem;
    /*position: fixed;*/
    /*left: 0;*/
    /*top: 0.9rem;*/
    /*bottom: 0;*/
    width: 100%;
    padding: 1.1rem 0.2rem 0;
    color: #333;
}

header.app-head {
    font-size: 0.3rem;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 0.9rem;
    line-height: 0.9rem;
    color: #000;
    background: white;
    z-index: 99;
    text-align: center;
    border-bottom: 1px solid #dddddd;
}
header.app-head > div:first-child, header.app-head > div:last-child {
    min-width: 0.9rem;
    max-width: 0.9rem;
}
header.app-head > div.title {
    font-size: 0.32rem;
}
header.app-head > div:first-child > span {
    font-size: 0.4rem;
}


/*content*/
.plugin-list {
    overflow: hidden;
    width: 100%;
    padding-bottom: 0.2rem;
}
.plugin-list > li {
    width: 100%;
    margin-bottom: 0.6rem;
}
.plugin-list > li > h3 {
    font-size: 0.3rem;
    font-weight: 500;
    margin-bottom: 0.1rem;
}
.plugin-list > li > div.text {
    background: #f1f1f1;
    padding: 0.2rem 0 0.2rem 0.3rem;
    line-height: 0.55rem;
    border-radius: 0.05rem;
}
.plugin-list > li > div.text > p {
    padding: 0.1rem 0;
}
.plugin-list > li > div.text > a {
    display: inline-block;
    text-decoration: underline;
    color: #00A0E8;
}
.plugin-list > li > div.text > input {
    width: 100%;
    border: none;
    line-height: 0.55rem;
    background: none;
    color: red;
}
.plugin-list > li > p.btn {
    font-size: 0.3rem;
    width: 100%;
    line-height: 0.9rem;
    margin-bottom: 0.3rem;
    text-align: center;
    background: #333;
    color: white;
    border-radius: 0.1rem;
}
pre {
    position: relative;
    font-size: 0.26rem;
    width: 100%;
    padding: 0.2rem;
    margin: 0 0 0.3rem;
    background: #f1f1f1;
    color: #333;
    border-radius: 0.05rem;
}
pre:after {
    font-size: 0.28rem;
    color: #cccccc;
    content: 'JS';
    position: absolute;
    right: 0.1rem;
    top: 0.1rem;
}

.plugin-list > li > div.image {
    overflow: hidden;
}
.plugin-list > li > div.image > div {
    float: left;
    width: 50%;
    height: 3.6rem;
    padding: 0.1rem;
}
.plugin-list > li > div.image > div > img {
    width: 100%;
    height: auto;
    border-radius: 0.05rem;
}


